	module.exports = {"data":{"markdownRemark":{"html":"<p><a href=\"https://nodejs.org/\">Node.js</a> is a JavaScript runtime built on <a href=\"https://developers.google.com/v8/\" title=\"Chrome&#x27;s V8 Engine\">Chrome’s V8 JavaScript engine</a> enabling JavaScript to be used for server-side programming.</p>\n<p>Node.js has become a key part of ‘JavaScript Everywhere’, encouraging web application development to unify around a single programming language, rather than rely on a different language for writing server side scripts.</p>\n<h3>Tutorials:</h3>\n<ul>\n<li><a href='http://nodeschool.io/' target='_blank' rel='nofollow'>NodeSchool.io interactive lessons</a></li>\n<li><a href='https://github.com/maxogden/art-of-node/#the-art-of-node' target='_blank' rel='nofollow'>The Art of Node (an introduction to Node)</a></li>\n<li><a href='http://nodeguide.com/' target='_blank' rel='nofollow'>Node.js guide</a></li>\n<li><a href='http://howtonode.org/express-mongodb' target='_blank' rel='nofollow'>Build a blog with Node.js, express and MongoDB</a></li>\n</ul>\n<h2>Books</h2>\n<ul>\n<li><a href='http://nodebeginner.org/' target='_blank' rel='nofollow'>The Node Beginner Book</a></li>\n<li><a href='https://github.com/visionmedia/masteringnode' target='_blank' rel='nofollow'>Mastering Node.js</a></li>\n<li><a href='http://chimera.labs.oreilly.com/books/1234000001808/index.html' target='_blank' rel='nofollow'>Up and Running with Node.js</a></li>\n<li><a href='https://www.packtpub.com/web-development/node-web-development' target='_blank' rel='nofollow'>Node Web Development</a></li>\n</ul>\n<h2>Courses</h2>\n<ul>\n<li><a href='http://node.codeschool.com/' target='_blank' rel='nofollow'>Real Time Web with Node.js</a></li>\n<li><a href='http://www.globalknowledge.co.uk/courses/it-subjects/application-development/' target='_blank' rel='nofollow'>Essential Node.js from DevelopMentor</a></li>\n<li><a href='https://www.edx.org/course/introduction-mongodb-using-mean-stack-mongodbx-m101x-0' target='_blank' rel='nofollow'>Introduction to MongoDB using the MEAN Stack</a></li>\n</ul>\n<h2>Blogs</h2>\n<ul>\n<li><a href='http://blog.nodejs.org/' target='_blank' rel='nofollow'>The Node.js blog</a></li>\n<li><a href='http://howtonode.org/' target='_blank' rel='nofollow'>How To Node</a></li>\n<li><a href='http://dailyjs.com/' target='_blank' rel='nofollow'>DailyJS</a></li>\n<li><a href='http://blog.nodejitsu.com/' target='_blank' rel='nofollow'>Nodejitsu blog</a></li>\n<li><a href='https://github.com/dankogai/js-deflate' target='_blank' rel='nofollow'>js-deflate</a> for gzipping of data to make it fit in URLs</li>\n<li><a href='http://www.wilcoxd.com/whitepapers/node_js/' target='_blank' rel='nofollow'>Ryan Wilcox’s Whitepaper</a></li>\n<li><a href='http://www.devthought.com/' target='_blank' rel='nofollow'>devthought</a></li>\n</ul>\n<h3>More Information:</h3>\n<ul>\n<li><a href='https://stackoverflow.com/questions/2353818/how-do-i-get-started-with-node-js' target='_blank' rel='nofollow'>Stackoverflow.com: How do I get started with Node.js?</a></li>\n<li><a href='https://nodejs.org/en/docs/guides/getting-started-guide/'  target='_blank' rel='nofollow'>Node.js Official Website: How do I start with Node.js after I installed it?</a></li>\n</ul>","fields":{"slug":"/miscellaneous/get-started-with-nodejs/"},"frontmatter":{"title":"Get Started with Nodejs"}}},"pathContext":{"slug":"/miscellaneous/get-started-with-nodejs/"}}

